import {
  CountriesProvider,
  ServicesProvider,
  StoreProvider,
  AddressFormFieldsProvider,
} from '@/checkout/context';
import { BackupService } from '@/checkout/services';
import { FunctionComponent } from 'preact';
import { Lang } from '@adobe/elsie/i18n';
import { LocalStorage } from '@/checkout/lib';
import { UIProvider } from '@adobe/elsie/components';
import { config } from '@/checkout/api';
import { events } from '@adobe/event-bus';
import { useEffect, useMemo, useState } from 'preact/hooks';
import { deepmerge } from '@adobe/elsie/lib/deepmerge';

import en_US from '../i18n/en_US.json';

// Langs
const langDefinitions = {
  default: en_US,
};

interface IntlProviderProps {
  children?: any;
}

export const IntlProvider: FunctionComponent<IntlProviderProps> = ({
  children,
}) => {
  const [locale, setLang] = useState<Lang>();

  const userLangDefinitions = config.getConfig()?.langDefinitions;

  useEffect(() => {
    const localeEvent = events.on(
      'locale',
      (payload) => {
        if (payload !== locale) setLang(payload as Lang);
      },
      { eager: true }
    );
    return () => {
      localeEvent?.off();
    };
  }, [locale]);

  // Merge language definitions with user language definitions
  const definitions = deepmerge(langDefinitions, userLangDefinitions ?? {});

  return (
    <UIProvider lang={locale} langDefinitions={definitions}>
      {children}
    </UIProvider>
  );
};

export const DataProvider: FunctionComponent<IntlProviderProps> = ({
  children,
}) => {
  const backupService = useMemo(() => {
    return new BackupService('DROPIN__CHECKOUT', new LocalStorage());
  }, []);

  return (
    <ServicesProvider services={{ backupService }}>
      <StoreProvider>
        <AddressFormFieldsProvider>
          <CountriesProvider>{children}</CountriesProvider>
        </AddressFormFieldsProvider>
      </StoreProvider>
    </ServicesProvider>
  );
};

export const Provider: FunctionComponent<IntlProviderProps> = ({
  children,
}) => {
  return (
    <DataProvider>
      <IntlProvider>{children}</IntlProvider>
    </DataProvider>
  );
};
